<template>
  <v-container>
    <page-title :title="'VolleyballLife Registration'"></page-title>
    <v-layout row wrap>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-card-title class="primary white--text">
            <h2>Join</h2>
          </v-card-title>
          <v-card-text>
            <v-form ref="form" lazy-validation>
              <v-text-field
                label="First Name"
                v-model="firstName"
                @blur="$v.firstName.$touch()"
                :rules="[() => $v.firstName.required || 'Your first name is required']"
                required
              ></v-text-field>
              <v-text-field
                label="Last Name"
                v-model="lastName"
                @blur="$v.lastName.$touch()"
                :rules="[() => $v.lastName.required || 'Your last name is required']"
                required
              ></v-text-field>
              <v-text-field
                label="Email"
                v-model="email"
                :error-messages="emailErrors"
                @blur="$v.email.$touch()"
                required
              ></v-text-field>
              <v-text-field
                label="Password"
                v-model="password"
                hint="At least 6 characters"
                min="6"
                :append-icon="hidePassword ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (hidePassword = !hidePassword)"
                :type="hidePassword ? 'password' : 'text'"
                :error-messages="passwordErrors"
                @blur="$v.password.$touch()"
                required
              ></v-text-field>
              <v-text-field
                label="Confirm password"
                v-model="passwordConfirm"
                :append-icon="hidePasswordConfirm ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (hidePasswordConfirm = !hidePasswordConfirm)"
                :type="hidePasswordConfirm ? 'password' : 'text'"
                :error-messages="passwordConfirmErrors"
                @blur="$v.passwordConfirm.$touch()"
                required
              ></v-text-field>
              <v-alert
                v-if="errors.length > 0"
                color="error"
                icon="warning"
                outline
                :value="true"
                transition="scale-transition"
              >
                <ul v-if="errors.length > 1">
                  <li v-for="(error,i) in errors" :key="i">
                    <span v-if="error.description">
                      {{error.description}}
                    </span>
                    <span v-else>
                      {{error}}
                    </span>
                  </li>
                </ul>
                <span v-else>
                    <span v-if="errors[0].description">
                      {{errors[0].description}}
                    </span>
                    <span v-else>
                      {{errors[0]}}
                    </span>
                </span>
              </v-alert>
              <v-layout>
                <v-flex text-xs-center>
                  <v-btn
                  color="color3"
                  :disabled="!valid || submitting"
                  :loading="submitting"
                  @click="submit">
                  Join
              </v-btn>
                </v-flex>
              </v-layout>
            </v-form>
          </v-card-text>
       </v-card>
      </v-flex>
    </v-layout>
  </v-container>  
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, minLength, email, sameAs } from 'vuelidate/lib/validators'
import vbl from '../../../VolleyballLife'

export default {
  mixins: [validationMixin],
  validations: {
    firstName: { required },
    lastName: { required },
    email: { required, email },
    password: { required, minLength: minLength(6) },
    passwordConfirm: { sameAsPassword: sameAs('password') }
  },
  data: () => ({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    hidePassword: true,
    passwordConfirm: '',
    hidePasswordConfirm: true,
    submitting: false,
    errors: []
  }),
  computed: {
    emailErrors () {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('Must be valid e-mail')
      !this.$v.email.required && errors.push('E-mail is required')
      return errors
    },
    passwordErrors () {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.required && errors.push('A password is required')
      !this.$v.password.minLength && errors.push('Your password must be at least 6 characters')
      !/\d/.test(this.password) && errors.push('Your password must have at least one digit (0-9).')
      return errors
    },
    passwordConfirmErrors () {
      const errors = []
      if (!this.$v.passwordConfirm.$dirty) return errors
      !this.$v.passwordConfirm.sameAsPassword && errors.push('Your passwords don\'t match')
      return errors
    },
    valid () { return !this.$v.$invalid }
  },
  methods: {
    submit () {
      if (this.$refs.form.validate()) {
        this.errors = []
        this.submitting = true
        const dto = {
          firstName: this.firstName,
          lastName: this.lastName,
          email: this.email,
          password: this.password
        }
        this.axios.post(vbl.user.register, dto)
        .then((response) => {
          this.$auth.setToken(response)
          this.$router.push('me')
        })
        .catch((error) => {
          var response = error.response.data
          if (response.errors) {
            this.errors = response.errors
            this.submitting = false
          } else {
            this.errors.push(response)
          }
        })
      }
    }
  }
}
</script>



